table.image-optimize {
  table-layout: fixed;
}

table.data thead th.io-headline-row {
  padding-top: 24px;
  padding-bottom: 24px;
}

.image-optimize h2 {
  font-weight: bold;
  color: #29323d;
}

table.data thead th.io-format-column {
  width: 20%;
}

table.data thead th.io-name-column {
  width: 20%;
}

table.data thead th.io-command-column {
  width: 50%;
}

table.data thead th.io-installed-column {
  width: 50%;
}

/**
 * This injects all of Tailwind's utility classes, generated based on your
 * config file.
 *
 */

.block {
  display: block;
}

.inline-block {
  display: inline-block;
}

.table {
  display: table;
}

.hidden {
  display: none;
}

.relative {
  position: relative;
}

.uppercase {
  text-transform: uppercase;
}

